
.container-error{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container-error img{
    max-width: 300px;
    margin: 24px 0;
    animation: shakes 1.5s
}

@keyframes shakes {
    10%, 90%{
        transform: translate3d(-1px, -1px, 0)
    }
    20%, 80%{
        transform: translate3d(2px, -1px, 0)
    }
    30%, 50%, 70%{
        transform: translate3d(-4px, -1px, 0)
    }
    40%, 60%{
        transform: translate3d(4px, -1px, 0)
    }
}

.container-error h1{
    color: #fff;
}

.container-error a{
    color: #132742;
    background-color: #fff;
    text-decoration: none;
    padding: 12px 18px;
    border-radius: 4px;
    margin: 14px 0;
    font-weight: bold;
}