.link-container{
    max-width: 1120px;
    margin: 32px auto 0 auto;
}

.links-header{
    display: flex;
    flex-direction: row;
    color:#fff
}

.links-header svg{
    margin-right: 14px;
}

.link-items{
    width: 700px;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 8px;
}

.link-items:last-child{
    margin-bottom: 18px;
}

.link{
    border: 0;
    width: 100%;
    min-width: 80%;
    border-radius: 4px;
    height: 46px;
    background-color: rgb(255, 255, 255,0.15);
    margin-right: 14px;
    display: flex;
    align-items: center;
    padding: 0 14px;
    font-size: 18px;
    color: #fff;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.link svg{
    margin-right: 14px;
}

.link-delete{
    border: 0;
    background: transparent;
}

.empty-text{
    width: 100%;
    text-align: center;
    margin-top: 28px;
    color: #fff;
}

@media (max-width: 715px){
    .link-container{
        width: 90%;
        margin-top: 90px;
    }

    .links-header h1{
        font-size: 40px;
    }

    .link-items{
        width: 100%;
    }

    .link{
        width: 100%;
    }
}