
.container-modal{
    position: absolute;
    bottom: 18px;
    background-color: #fff;
    padding: 10px;
    width: 700px;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    animation: slideY 1.5s;
}

@keyframes slideY {
    from{
        transform: translateY(90px);
    }
    to{
        transform: translateY(0);
    }
}

.modal-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.modal-header button{
    border: 0;
    background: transparent;
}

.container-modal span{
    margin: 14px 0;
    color: #a7a7a7;
}

.modal-link{
    background-color: #172742;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 14px;
    height: 38px;
    border: 0;
    border-radius: 4px;
    color: #fff;
    font-size: 15px;
}

@media (max-width: 735px){
    .container-modal{
        width: 90%;
    }
}